@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
.bg-blue{background-color:#23A3D8}.bg-blue h1,.bg-blue p{color:white}.bg-blue p{font-size:20px;margin:0}

.base{font-family:"Ubuntu",sans-serif}.b-700{font-weight:700}.f-15{font-size:15px}

.card{border-radius:20px !important;box-shadow:0px -1px 11px 8px rgba(0,0,0,0.07);-webkit-box-shadow:0px -1px 11px 8px rgba(0,0,0,0.07);-moz-box-shadow:0px -1px 11px 8px rgba(0,0,0,0.07)}.card .image-size{color:#23A3D8;width:7em;height:auto}.card ul{padding:0;list-style-type:none;font-size:24px}.card .stack{border-bottom:3px solid #23A3D8}.card:hover{box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);-webkit-box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);-moz-box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);transition:.3s ease}

.project-base-card{cursor:pointer}.project-card{border-radius:15px}.project-card__container{position:relative;text-align:center}.project-card__img{max-width:100%;height:50%;border-radius:15px}.project-card__title{position:absolute;width:100%;height:100%;top:0;left:0;background-color:rgba(35,162,216,0.541);transition:.3s ease;opacity:0}.project-card__title-position{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%)}.project-card h2{color:white}.project-card__container:hover .project-card__title{opacity:1}

